<div class="new-project"><div class="new-project__title">Новый проект</div><div class="new-project__controls"><div class="new-project__inputs"><base-input class="new-project__input" placeholder="Имя файла" v-model="this.project.title" :error="validation.firstError('project.title')"></base-input><base-input class="new-project__input" placeholder="Вид схемы БД" v-model="this.project.DBSheme" :error="validation.firstError('project.DBSheme')"></base-input></div><div class="new-project__send"><base-button class="send__btn" customType="fill" @click="saveProject">Создать</base-button><base-button class="send__btn" customType="fill" @click="$emit('changeCardView')">Отмена</base-button></div></div></div>